<script>
import Navbar from './components/Navbar';
import { vueTopprogress } from 'vue-top-progress';

export default {
    components: {
        Navbar,
        vueTopprogress
    },

    data(){
        return{
            ws:{
                url: "",
                connected: false
            },
            stats:{
                enabled: false,
                hardware: null,
                chipId: null,
                sketchHash: null,
                macAddress: null,
                freeHeap: null,
                heapFragmentation: null,
                wifiMode: null
            },
            cards:{
                temperature: [],
                humidity: [],
                status: [],
                button: [],
                number: [],
                lineChart: []
            }
        }
    },

    methods:{
        rebootMicrocontroller(){
        },

        getRandomInt(min, max) {
            min = Math.ceil(min);
            max = Math.floor(max);
            return Math.floor(Math.random() * (max - min)) + min; //The maximum is exclusive and the minimum is inclusive
        }
    },

    mounted(){
        this.cards.temperature.push({
            id: 'temp1',
            name: 'Temperature Card',
            type: 0,
            value: 20
        });

        this.cards.humidity.push({
            id: 'hum1',
            name: 'Humidity Card',
            value: 43
        });

        this.cards.status.push({
            id: 'stat1',
            name: 'Code Status',
            value: 1
        });

        this.cards.lineChart.push({
            id: 'line1',
            name: 'Power Usage',
            xAxis: ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
            yAxis: [2, 4, 9, 16, 10, 13, 6],
            yAxisName: 'Power (KwH)'
        });
    }
}
</script>

